import React, {useState} from 'react';
import ScrollToTop from '../components/ScrollToTop';
import Story from '../components/Stories';
import Store from '../stores/Store';
import { createTheme } from '@material-ui/core/styles';
import { StylesProvider, ThemeProvider, jssPreset } from "@material-ui/styles";
import { create } from "jss";
import rtl from "jss-rtl";


const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const theme = createTheme({
  direction: "rtl"
});

const reviewStore = new Store();

function Stories(props) {    
    const [isOpen, setIsOpen]= useState(false)
    const store = new Store();
    const toggle = () => {
      setIsOpen(!isOpen)
    }
    // console.log("in page  !! ",props.location.state.selectedSite)
    // console.log("in page onlt props!! ",props)
let selectedSite=props?.location?.state?.selectedSite? props.location.state.selectedSite: null;
    return (
      <div id="storiesPageStyle" > 
          <StylesProvider jss={jss}>
                <ThemeProvider theme={theme}>
                    <ScrollToTop />
                      <Story reviewStore={reviewStore} userObs={props.userObs} 
                             store={store} selectedSite={selectedSite}/>
                </ThemeProvider>
          </StylesProvider>
      </div>
    )
}

export default Stories;
