import React, {useState} from 'react';
import ScrollToTop from '../components/ScrollToTop';
import TresurHuntTamplate from '../components/TresurHuntTamplate';


import { createTheme } from '@material-ui/core/styles';
import { StylesProvider, ThemeProvider, jssPreset } from "@material-ui/styles";
import { create } from "jss";
import rtl from "jss-rtl";


const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const theme = createTheme({
  direction: "rtl"
});


function TresurHuntTamplatePage(props) {

    const [isOpen, setIsOpen]= useState(false)
  
    const toggle = () => {
      setIsOpen(!isOpen)
    }
    let score=0;
    let qNum=0;
    let thID;
    let dataQuestions;
    let answers;
    let correct;
    let question;
    let totalQuestions;
    // let qNumNext=0;

    if( props?.location?.state?.flag===true){
         score = props.location.state.score;
         qNum = props.location.state.qNum;
         thID = props.location.state.thID;
         totalQuestions = props.location.state.totalQuestions;
         answers = props.location.state.answers;
         correct = props.location.state.correct;
         question = props.location.state.question;
         dataQuestions={answers:answers, correct:correct, question:question}
        //  console.log("TresurHuntTamplateQuestPageStyle",score)
        // console.log("before answers",props.location.state.answers)
        // let swapTemp= answers[correct-1];
        // answers[correct]=answers[0];
        // answers[0]=swapTemp;
        //  let temp={answers:answers,correct:0};
        //  let swapTemp= answers[correct-1];
        //  temp.answers[correct-1]=temp.answers[0];
        //  temp.answers[0]=swapTemp;
        //  console.log("before temp",temp)
        // shuffle(temp);
        // console.log("after temp",temp)
        // answers=temp.answers;
        // correct=temp.correct+1;
        // console.log("after answers",answers)
    }

    function shuffle(temp) {
      let array=temp.answers;
      let currentIndex = array.length,  randomIndex;
        
      // While there remain elements to shuffle...
      while (currentIndex !== 0) {
    
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
    
        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex], array[currentIndex]];
  
        //follow the correct answer
        if(temp.correctAnswer===currentIndex)
          temp.correctAnswer=randomIndex;
        if(temp.correctAnswer===randomIndex)
          temp.correctAnswer=currentIndex;
        
      }
      return array;
    }

    
    return (
      <div id="TresurHuntTamplateQuestPageStyle" className="bodyStandartHigher">
          <StylesProvider jss={jss}>
              <ThemeProvider theme={theme}>
                  <ScrollToTop /> 
                        <TresurHuntTamplate myqNum={qNum} myScore={score} isOnlyQuiz={false} 
                          thID={thID} dataQuestions={dataQuestions} totalQuestions={totalQuestions}/>
              </ThemeProvider>
          </StylesProvider>
        </div>
    )
}

export default TresurHuntTamplatePage
